<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 产品编码 -->
            <a-form-model-item
              label="产品编码"
              prop="productCode"
              ref="productCode"
            >
              <a-input
                v-model="entity.productCode"
                placeholder="请输入产品编码，格式为P开头加4位数字"
                name="productCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品名称 -->
            <a-form-model-item
              label="产品名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入产品名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品分类 -->
            <a-form-model-item
              label="产品分类"
              prop="category"
              ref="category"
            >
              <a-select
                v-model="entity.category"
                placeholder="请选择产品分类"
                name="category"
              >
                <a-select-option value="电子设备">电子设备</a-select-option>
                <a-select-option value="智能家居">智能家居</a-select-option>
                <a-select-option value="网络设备">网络设备</a-select-option>
                <a-select-option value="办公设备">办公设备</a-select-option>
                <a-select-option value="安防设备">安防设备</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 制造商 -->
            <a-form-model-item
              label="制造商"
              prop="manufacturer"
              ref="manufacturer"
            >
              <a-select
                v-model="entity.manufacturer"
                placeholder="请选择制造商"
                name="manufacturer"
              >
                <a-select-option value="华为">华为</a-select-option>
                <a-select-option value="小米">小米</a-select-option>
                <a-select-option value="联想">联想</a-select-option>
                <a-select-option value="戴尔">戴尔</a-select-option>
                <a-select-option value="惠普">惠普</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 产品型号 -->
            <a-form-model-item
              label="产品型号"
              prop="model"
              ref="model"
            >
              <a-input
                v-model="entity.model"
                placeholder="请输入产品型号"
                name="model"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 价格 -->
            <a-form-model-item
              label="价格(元)"
              prop="price"
              ref="price"
            >
              <a-input-number
                v-model="entity.price"
                :min="0"
                :precision="2"
                :step="100"
                style="width: 100%"
                placeholder="请输入产品价格"
                name="price"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 库存数量 -->
            <a-form-model-item
              label="库存数量"
              prop="stockQuantity"
              ref="stockQuantity"
            >
              <a-input-number
                v-model="entity.stockQuantity"
                :min="0"
                :precision="0"
                style="width: 100%"
                placeholder="请输入库存数量"
                name="stockQuantity"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="在售">在售</a-radio>
                <a-radio value="预售">预售</a-radio>
                <a-radio value="停售">停售</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 产品描述 -->
            <a-form-model-item 
              label="产品描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入产品描述"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 产品规格 -->
      <g-collapse headerName="产品规格">
        <template #content>
        <a-row :gutter="24">
          <a-col :sm="24">
              <a-button class="add-spec-btn" type="primary" icon="plus" @click="addSpec">添加规格</a-button>
          </a-col>
          <a-col :sm="24">
            <a-table 
              :columns="specColumns" 
              :dataSource="entity.specifications" 
              :pagination="false"
              size="small"
              rowKey="id"
            >
              <template slot="specName" slot-scope="text, record, index">
                <a-input v-model="entity.specifications[index].specName" placeholder="请输入规格名称" />
              </template>
              <template slot="specValue" slot-scope="text, record, index">
                <a-input v-model="entity.specifications[index].specValue" placeholder="请输入规格值" />
              </template>
              <template slot="action" slot-scope="text, record, index">
                <a-button type="danger" icon="delete" size="small" @click="() => removeSpec(index)"></a-button>
              </template>
            </a-table>
          </a-col>
        </a-row>
    </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditProduct",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        productCode: "",
        name: "",
        category: "",
        manufacturer: "",
        model: "",
        price: 0,
        stockQuantity: 0,
        description: "",
        status: "在售",
        specifications: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        productCode: [
          { required: true, message: "请输入产品编码", trigger: "change" },
          { pattern: /^P\d{4}$/, message: "产品编码必须为P开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入产品名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "产品名称不能包含前后空格", trigger: "change" }
        ],
        category: [
          { required: true, message: "请选择产品分类", trigger: "change" }
        ],
        manufacturer: [
          { required: true, message: "请选择制造商", trigger: "change" }
        ],
        model: [
          { required: true, message: "请输入产品型号", trigger: "change" }
        ],
        price: [
          { required: true, message: "请输入产品价格", trigger: "change" },
          { type: "number", min: 0, message: "价格不能小于0", trigger: "change" }
        ],
        stockQuantity: [
          { required: true, message: "请输入库存数量", trigger: "change" },
          { type: "number", min: 0, message: "库存数量不能小于0", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      specColumns: [
        {
          title: "规格名称",
          dataIndex: "specName",
          key: "specName",
          width: "40%",
          scopedSlots: { customRender: "specName" }
        },
        {
          title: "规格值",
          dataIndex: "specValue",
          key: "specValue",
          width: "40%",
          scopedSlots: { customRender: "specValue" }
        },
        {
          title: "操作",
          key: "action",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取产品数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        productCode: "",
        name: "",
        category: "",
        manufacturer: "",
        model: "",
        price: 0,
        stockQuantity: 0,
        description: "",
        status: "在售",
        specifications: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保specifications数组被正确初始化
          specifications: row.specifications || []
        };
      }
    },
    
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        // 检查规格表，确保每个规格都有名称和值
        const invalidSpec = this.entity.specifications.find(
          spec => !spec.specName || !spec.specValue
        );
        
        if (invalidSpec) {
          this.$message.error("所有规格必须有名称和值");
          return;
        }

        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    addSpec() {
      this.entity.specifications.push({
        id: Date.now(), // 使用时间戳作为临时ID
        specName: "",
        specValue: ""
      });
    },
    
    removeSpec(index) {
      this.entity.specifications.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-spec-btn {
  margin-bottom: 16px;
}
</style> 